<<template>
     <v-main class='main'>
    <v-row class='rows' >
     <v-col md='4' sm='4' xs='6' lg='3' xl='3'  v-for='item in items' :key='item'><Card :item="item" /></v-col>  
    </v-row>
     </v-main>
</template>

<script>
import store from '../../store/index'
import Card from './Card.vue'
export default {
    name: 'MainArea',
    data () {
        return {
            items: []
        }
    }, 
    components : {
       Card
    },
    created () {
        var data = store.state.items;
        const { Fruits } = data;
        this.items = Fruits.items; 
    }
}

</script>

<style lang="scss" scoped>
.main {
    margin-top: 3% !important;
    margin-right: 3vw !important;
}
</style>